<template>
  <div>
    <vSearch
      @getOption="getOption"
      @searchInfo="searchInfo"
      @clearSearchInfo="clearSearchInfo"
      :pageType="pageType"
    ></vSearch>
    <el-table
      :data="tableData"
      style="width: 100%"
      max-height="675"
      :default-sort="{prop: 'date', order: 'descending'}"
    >
      <el-table-column
        prop="contractNum"
        label="合同编号"
        sortable
        width="180"
      >
      </el-table-column>
      <el-table-column
        prop="wareHouseId"
        label="仓单编号(仓单总数)"
        width="220"
      >
      </el-table-column>
      <el-table-column
        prop="customerName"
        label="交易客户"
        sortable
        width="180"
      >
      </el-table-column>
      <el-table-column
        prop="goodsType"
        label="仓单货物品种"
        sortable
        width="120"
      >
      </el-table-column>
      <el-table-column
        prop="customerId"
        label="仓单总价值/总现价"
        sortable
        width="200"
      >
      </el-table-column>
      <el-table-column
        label="保值方案"
        sortable
        width="120"
      >
      </el-table-column>
      <el-table-column
        prop="price"
        label="对应总放款金额"
        sortable
        width="140"
      >
      </el-table-column>
      <el-table-column
        prop="clearDate"
        label="结清日期"
        sortable
        width="105"
      >
      </el-table-column>
    </el-table>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-size="pageSize"
        layout="prev, pager, next, jumper"
        :total="totalPage"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
import vSearch from "../components/Search.vue";

export default {
  data() {
    return {
      searchForm: {
        option: "",
        inputText: ""
      },
      pageType: 4,
      tableData: [
        {
          customerId: "hz-hzjylxmyyxgs-180926",
          customerName: "杭州久益隆鑫贸易有限公司",
          wareHouseId: "al0260025533~al0260025548",
          money: "522583334",
          contractNum: "NHZB-CDZY-B-AL-180001",
          goodsType: "铝",
          price: "6,596,691.4",
          duration: "30",
          clearDate: "2018-10-21"
        }
      ],

      currentPage: 1,
      pageSize: 1,
      totalPage: 100
    };
  },
  methods: {
    getOption(value, inputText) {
      this.searchForm.option = value;
      this.searchForm.inputText = inputText;
    },
    searchInfo() {
      console.log(this.searchForm.option + "+" + this.searchForm.inputText);
    },
    clearSearchInfo() {
      this.searchForm.option = "";
      this.searchForm.inputText = "";
    },
    formatter(row, column) {
      return row.address;
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    }
  },
  components: {
    vSearch
  }
};
</script>

<style>
.pagination {
  text-align: center;
}
</style>



